.imageContainer {
    overflow: hidden;
    background-color: black;
    height: 220px;
    border-radius: 10px;
    position: relative;
    cursor: pointer;
  }
  
  .userInformation {
    position: absolute;
    bottom: 0;
    margin: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .userInformation img {
    border-radius: 100%;
    width: 30px;
    height: 30px;
  }
  
  .userInformation p {
    display: inline;
    margin-left: 10px;
    color: white;
    font-weight: 500;
    text-align: left;
  }
  
  .userInformation p span {
    color: #0fddaf;
  }
  